<template>
  <transition name="fade">
    <div class="container mx-auto">
      <div class="app-emergency-page p-5">
        <h3 class="text-green-600 text-base">EMERGENCY</h3>
      </div>
      <div>
        <form class="px-5">
          <div class="relative">
            <select
              class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
              id="grid-state"
              v-model="selectedCategory"
            >
              <option>Fire</option>
              <option>Police</option>
              <option>Hospital</option>
            </select>
            <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
              <svg
                class="fill-current h-4 w-4"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 20 20"
              >
                <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" /></svg>
            </div>
          </div>
          <div class="pt-2">
            <ul class="flex emergency-images">
              <li
                class="flex-1"
                v-on:dblclick="deleteImage(0)"
              >
                <img :src="imagesToDisplay[0]">
              </li>
              <li
                class="flex-1"
                v-on:dblclick="deleteImage(1)"
              >
                <img :src="imagesToDisplay[1]">
              </li>
              <li
                class="flex-1"
                v-on:dblclick="deleteImage(2)"
              >
                <img :src="imagesToDisplay[2]">
              </li>
              <li
                class="flex-1"
                v-on:dblclick="deleteImage(3)"
              >
                <img :src="imagesToDisplay[3]">
              </li>
            </ul>
          </div>
          <div>
            <textarea
              class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
              v-model="remarks"
              cols="30"
              rows="10"
              placeholder="Remarks"
            ></textarea>
          </div>
          <input
            ref="file"
            class="file-upload"
            type="file"
            @change="onFilePicked"
            accept="image/*"
            capture="environment"
          >
          <div class="mt-2">
            <button
              @click.prevent="captureImage"
              class="bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-4 rounded"
            >Capture</button>
          </div>
        </form>
        <div>
          <button class="bg-green-500 hover:bg-green-600 text-white font-bold mt-5 py-3 px-6 rounded">EMERGENCY CALL</button>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  data () {
    return {
      images: [],
      imagesToDisplay: [],
      remarks: '',
      selectedCategory: 'Fire'
    }
  },
  methods: {
    logout () {
      this.$store.dispatch('logout')
        .then(() => {
          this.$router.push('/login')
        })
    },
    toggleModal () {
      this.modal = !this.modal
    },
    captureImage () {
      if (this.imagesToDisplay.length < 4) {
        this.$refs.file.click()
      }
    },
    onFilePicked (e) {
      const files = e.target.files
      if (files[0] !== undefined) {
        const fr = new FileReader()
        fr.readAsDataURL(files[0])
        fr.addEventListener('load', () => {
          console.log({ fr: fr.result })
          this.imagesToDisplay.push(fr.result)
          this.images.push(files[0])
        })
      }
    },
    deleteImage (index) {
      if (this.imagesToDisplay[index]) {
        this.images.splice(index, 1)
        this.imagesToDisplay.splice(index, 1)
      }
    }
  }
}
</script>
<style lang="scss">
.file-upload {
  display: none;
}
.emergency-images {
  height: 100px;
  li {
    background-color: #e3e3e3;
    height: 90px;
    width: 80px;
    &:not(:first-child) {
      margin-left: 5px;
    }
  }
  img {
    height: 90px;
    width: 80px;
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>
